.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.owl-dots {
  position: absolute;
  bottom: -30px;
  left: 0;
  right: 0;
  text-align: center;
}
.owl-dot {
  width: 14px;
  height: 14px;
  border-radius: 50%;
  background: #1b5773;
  transform: scale(0.4);
  transition: transform 0.2s;
  margin: 2px;
  display: inline-block;
}
.owl-dot:hover,
.owl-dot.active {
  transform: scale(1);
  background: #a03339;
}



