.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-pager {
  border-top: 2px solid #e5e7dc;
  border-bottom: 2px solid #e5e7dc;
  text-align: center;
  padding: 20px 0;
  margin: 20px 0 40px;
}
.b-pager__link {
  margin: 0 10px;
  padding: 5px 15px;
  color: #25769c;
  text-decoration: none !important;
  display: inline-block;
}
.b-pager__link--current {
  background: #fae5e0;
  color: #1b5773;
}
.b-pager__link-start {
  float: left;
  display: inline-block;
}
.b-pager__link--first,
.b-pager__link--prev {
  padding-left: 0;
  margin-left: 0;
}
.b-pager__link-pages {
  display: inline-block;
}
.b-pager__link-end {
  float: right;
  display: inline-block;
}
.b-pager__link--last,
.b-pager__link--next {
  padding-right: 0;
  margin-right: 0;
}
@media (max-width: 1206px) {
  .b-pager__link-start {
    float: none;
    display: block;
  }
  .b-pager__link-pages {
    display: block;
  }
  .b-pager__link-end {
    float: none;
    display: block;
  }
  .b-pager__link--first,
  .b-pager__link--prev,
  .b-pager__link--last,
  .b-pager__link--next {
    margin: 0 10px;
    padding: 5px 15px;
  }
}



