.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-main-services__title {
  font: 30px/30px "BlissPro";
  color: #fff;
  display: inline-block;
  margin: 0 10px 0 0;
  padding: 0;
}
.b-main-services__head {
  padding: 50px 50px 30px;
  margin-right: 50px;
  border-bottom: 3px solid #d8d3ab;
}
.b-main-services__body {
  padding: 30px 30px 30px 0;
}
@media (max-width: 1199px) {
  .b-main-services__head {
    padding: 50px 50px 10px 20px;
    margin-left: 30px;
    border-bottom: 2px solid #d8d3ab;
  }
}
@media (max-width: 992px) {
  .b-main-services__head {
    padding: 50px 50px 10px 0;
    margin-left: 50px;
    border-bottom: 2px solid #d8d3ab;
  }
  .b-main-services__body {
    padding-right: 50px;
    padding-left: 50px;
  }
}
@media (max-width: 767px) {
  .b-main-services {
    text-align: center;
    padding-bottom: 100px;
    position: relative;
  }
  .b-main-services__head {
    padding: 50px 0 30px;
    margin: 0 15px;
  }
  .b-main-services__body {
    font-size: 18px;
  }
  .b-main-services__link {
    bottom: 10px;
    left: 15px;
    right: 15px;
    padding: 26px 0 22px;
    border-top: 2px solid #d8d3ab;
    display: block;
    position: absolute;
  }
}



