.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
hr {
  border: none;
  background: #f6cec1;
  height: 2px !important;
  width: 100%;
  display: block;
  margin: 20px 0;
}
hr.hr--light {
  background: #e6e7dc;
}



