.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-callback--header {
  color: #93151d;
  font: 17px "BlissProLight";
  text-decoration: underline;
}
@media (max-width: 1199px) {
  .b-callback--header {
    top: 50px;
    left: 30px;
    font: 15px "BlissProLight";
    position: absolute;
  }
}
@media (max-width: 767px) {
  .b-callback--header {
    display: none;
  }
}



