.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-content-dd {
  margin: 10px 0 50px;
}
.b-content-dd__links {
  margin-bottom: 20px;
}
.b-content-dd__links--block .b-content-dd-link {
  margin: 15px 16px 15px 0 !important;
}
.b-content-dd__links--block .b-content-dd-link::after {
  content: "";
  display: block;
  width: 100%;
  height: 1px;
}
.b-content-dd__links--block .b-content-dd-link--open::after {
  margin-bottom: 30px;
}
.b-content-dd-link {
  color: #1b5773;
  margin-right: 40px;
  text-decoration: none !important;
  font: 21px/21px "WaldorfLightItalic";
  display: inline-block;
  position: relative;
}
.b-content-dd-link:hover,
.b-content-dd-link:focus,
.b-content-dd-link:active {
  color: #1b5773;
}
.b-content-dd-link::before {
  content: "\f107";
  font-family: "FontAwesome", sans-serif;
  color: #a03339;
  right: -15px;
  position: absolute;
}
.b-content-dd-link--open {
  border-color: transparent;
  color: #000;
}
.b-content-dd-link--open:hover,
.b-content-dd-link--open:focus,
.b-content-dd-link--open:active {
  color: #000;
}
.b-content-dd-link--open::before {
  content: "\f106";
}
.b-content-dd-link--not-desc {
  border: none;
  cursor: default;
}
.b-content-dd-link--not-desc::before {
  display: none;
}
.b-content-dd-link__txt {
  border-bottom: 1px dotted #1b5773;
}
.b-content-dd-item {
  display: none;
}
.b-content-dd-item--open {
  display: block;
  padding-bottom: 4px;
  margin: -18px 0 0;
}
@media (max-width: 767px) {
  .b-content-dd-link {
    width: 100%;
    margin: 10px 0;
    border-bottom: none !important;
    display: block;
  }
  .b-content-dd-link::before {
    position: static;
  }
}



