.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-contacts-list {
  text-align: left;
}
.b-contacts-list__item {
  margin-bottom: 15px;
}
.b-contacts-list__item a {
  display: block;
  color: #1b5773;
  font: 16px/22px "BlissPro";
}
@media (max-width: 767px) {
  .b-contacts-list {
    width: 186px;
    margin: 0 auto;
  }
  .b-contacts-list a {
    font-size: 14px;
  }
}



