.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-main-contacts {
  padding: 50px 10px 50px 48px;
}
.b-main-contacts__title {
  color: #000;
  font: 30px/30px "BlissPro";
  margin-bottom: 15px;
}
@media (max-width: 992px) {
  .b-main-contacts {
    padding: 50px 10px 50px 30px;
  }
  .b-main-contacts__title {
    color: #000;
    font: 30px/30px "BlissPro";
    margin-bottom: 15px;
  }
}
@media (max-width: 767px) {
  .b-main-contacts {
    padding: 50px 0;
    text-align: center;
  }
}



