.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.btn {
  border-radius: 3px !important;
}
.btn--doctor {
  margin: 4px 0;
  width: 92%;
}
.btn-primary {
  background: #a03339 !important;
  border: 1px solid #a03339 !important;
  color: #fff !important;
  font: 15px "BlissPro";
}
.btn-primary:hover,
.btn-primary:focus,
.btn-primary:active {
  background: #b63c41 !important;
  border: 1px solid #b63c41 !important;
}
.btn-secondary {
  background: #1b5773 !important;
  border: 1px solid #1b5773 !important;
  color: #fff !important;
  font: 15px "BlissPro";
}
.btn-secondary:hover,
.btn-secondary:focus,
.btn-secondary:active {
  background: #2b6783 !important;
  border: 1px solid #2b6783 !important;
}
.btn-warning {
  background: #d8d3ab !important;
  border: 1px solid #d8d3ab !important;
  color: #fff !important;
  font: 15px "BlissPro-Bold";
}
.btn-warning:hover,
.btn-warning:focus,
.btn-warning:active {
  background: #c7c29a !important;
  border: 1px solid #c7c29a !important;
}
.btn--fixed-width {
  font-size: 20px;
  margin-bottom: 15px;
  width: 250px;
  text-transform: uppercase;
}



