.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-table {
  margin: 30px 0;
}
.b-table td {
  padding: 3px 15px;
  color: #333;
  border-bottom: 1px solid #e6e8dd;
  border-right: 1px solid #e6e8dd;
  vertical-align: middle;
}
.b-table td:last-child {
  border-right: none;
}
.b-table tr:hover {
  background: #fbf0ec;
}
.b-table tr:last-child td {
  border-bottom: 2px solid #e6e8dd;
}
.b-table th {
  font-size: 0.85em;
  color: #000;
  border-top: 2px solid #e6e8dd;
  border-bottom: 2px solid #e6e8dd !important;
  border-right: 1px solid #e6e8dd;
  background: #fff !important;
}
.b-table th:last-child {
  border-right: none;
}



