.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.menu-cols {
  display: block;
  padding: 0 100px;
  background: #f0f0f0;
  border: 2px solid #1b5773;
  border-top: 0;
  border-radius: 0;
  position: relative;
}
.menu-cols::after {
  content: "";
  clear: both;
  width: 100%;
  height: 1px;
  display: block;
}
.menu-cols--with-banner {
  padding: 30px 0 30px 100px;
  box-shadow: -300px 0 0 #fff inset;
}
.menu-col {
  width: 30%;
  vertical-align: top;
  display: inline-block;
}
.menu-cols--with-banner .menu-col {
  width: 260px;
}
.menu-col__link {
  display: flex;
  align-items: center;
  padding: 4px 0;
  position: relative;
}
.menu-col__link--with-img {
  padding-left: 70px;
  min-height: 65px;
}
.menu-col__link span {
  color: #1b5773;
  font-size: 17px;
  display: block;
}
.menu-col__img {
  width: 60px;
  height: 60px;
  top: 0;
  left: 5px;
  display: block;
  position: absolute;
}
.menu-banner {
  float: right;
  width: 300px;
  margin-top: -30px;
}
.menu-banner__img {
  height: 160px;
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
}
.menu-banner__text {
  padding: 20px;
}
@media (max-width: 992px) {
  .menu-cols--with-banner .menu-col {
    display: block;
    padding-top: 0;
    padding-bottom: 0;
  }
  .menu-cols--with-banner .menu-col:last-child {
    padding-bottom: 30px;
  }
  .menu-cols--with-banner .menu-col:nth-child(2) {
    padding-top: 30px;
  }
}
.b-nav-toggler {
  display: none;
}
@media (max-width: 767px) {
  .b-nav-toggler {
    top: 20px;
    left: 15px;
    cursor: pointer;
    display: block;
    position: absolute;
  }
  .b-nav-toggler .fa {
    color: #838383;
    font-size: 26px;
  }
  .b-nav-toggler:focus .fa,
  .b-nav-toggler:active .fa,
  .b-nav-toggler:hover .fa {
    color: #a03339;
  }
  .b-nav-toggler .fa-times {
    color: #fff !important;
    position: fixed;
    top: 20px;
    left: 15px;
    z-index: 10000;
  }
}
.b-nav {
  background: #1b5773;
  position: relative;
}
.b-nav .nav {
  display: flex;
  justify-content: space-around;
}
.b-nav .nav-pills .nav-link {
  color: #fff;
  font: 14px/50px "BlissPro-Bold";
  padding: 0 30px;
  text-align: center;
  overflow: hidden;
  background: none !important;
  text-transform: uppercase;
  display: block;
  position: relative;
}
.b-nav .nav-pills .nav-link:hover,
.b-nav .nav-pills .nav-link:active,
.b-nav .nav-pills .nav-link:focus,
.b-nav .nav-pills .nav-link.active {
  background: none !important;
  position: relative;
  z-index: 1;
}
.b-nav .nav-pills .nav-link:hover::before,
.b-nav .nav-pills .nav-link:active::before,
.b-nav .nav-pills .nav-link:focus::before,
.b-nav .nav-pills .nav-link.active::before {
  content: "";
  display: block;
  position: absolute;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  background-image: url(~img/common/nav/link-active-bg.png);
  background-position: center bottom;
  background-repeat: no-repeat;
  background-size: 90%;
  width: 100%;
  height: 36px;
  z-index: 0;
}
.b-nav .nav-pills .nav-link__dropdown-icon {
  width: 12px;
  height: auto;
  color: #d8d3ab;
  border: none !important;
  display: inline-block;
  vertical-align: middle;
}
.b-nav .dropdown-toggle::after {
  display: none !important;
}
.b-nav .dropdown {
  position: static;
}
.b-nav .dropdown-menu {
  left: 0;
  right: 0;
  top: 48px;
  background: none;
  padding: 0 !important;
  border: none !important;
  z-index: 100;
}
.nav-pills .dropdown:hover,
.nav-pills .dropdown.open {
  overflow: visible !important;
}
.nav-pills .dropdown:hover .dropdown-menu,
.nav-pills .dropdown.open .dropdown-menu {
  display: block !important;
}
.nav-pills .dropdown:hover .nav-link,
.nav-pills .dropdown.open .nav-link {
  z-index: 9999 !important;
  overflow: visible !important;
}
.nav-pills .dropdown:hover .nav-link::after,
.nav-pills .dropdown.open .nav-link::after {
  content: "";
  display: block !important;
  position: absolute;
  top: 51px;
  left: 0;
  right: 0;
  width: auto !important;
  z-index: 9999;
  border: none !important;
  transform: rotate(180deg);
  background-image: url(~img/common/nav/link-active-bg.png);
  background-position: center bottom;
  background-repeat: no-repeat;
  background-size: 90%;
  height: 36px;
  opacity: 0.5;
}
@media (max-width: 1199px) {
  .b-nav .nav-pills .nav-link {
    padding: 0 15px;
  }
}
@media (max-width: 992px) {
  .b-nav .nav-item:first-child {
    margin-left: 0;
  }
  .b-nav .nav {
    padding-left: 0;
    padding-right: 0;
  }
  .b-nav .nav-pills .nav-link {
    padding: 0 6px;
  }
  .b-nav .dropdown-toggle::after {
    top: 16px;
    left: 50%;
    margin-left: -6px;
    display: block;
    position: absolute;
  }
}
@media (max-width: 767px) {
  .b-nav {
    display: none;
  }
  .b-nav.b-nav--open {
    background: rgba(27, 87, 115, 0.9);
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    z-index: 100;
    display: flex;
    flex-direction: row;
    align-items: center;
    align-content: center;
  }
  .b-nav.b-nav--open .nav {
    display: block;
    text-align: center;
    align-self: center;
    margin: 0 auto;
  }
  .b-nav.b-nav--open .nav-item {
    display: block;
    float: none;
    background: none !important;
  }
  .b-nav.b-nav--open .nav-link {
    display: block;
    font-size: 21px;
    background: none !important;
  }
  .b-nav.b-nav--open .nav-link::before,
  .b-nav.b-nav--open .nav-link::after {
    display: none !important;
  }
  .b-nav.b-nav--open .dropdown-toggle::after {
    position: static;
    display: inline-block;
  }
  .b-nav.b-nav--open .nav-link__dropdown-icon {
    display: none !important;
  }
  .nav-pills .dropdown:hover .dropdown-menu,
  .nav-pills .dropdown.open .dropdown-menu {
    display: none !important;
  }
  .nav-pills .dropdown:hover .nav-link::after,
  .nav-pills .dropdown.open .nav-link::after {
    display: none !important;
  }
}



