.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-services-carousel {
  margin: 20px 0;
}
.b-services-carousel__item {
  padding: 0;
}
.b-services-carousel__item i {
  font: 16px/21px "WaldorfLightItalic";
  font-style: normal;
  text-align: center;
  display: block;
  color: #1b5773;
}
.b-services-carousel__item img {
  height: 128px !important;
  width: 128px !important;
  display: block;
  margin: 0 auto 10px;
}
.b-services-carousel .owl-stage {
  white-space: nowrap;
}
.b-services-carousel .owl-item {
  display: inline-block;
  float: none;
  vertical-align: top;
  white-space: normal;
}
.b-services-carousel .owl-dots {
  display: none;
}
@media (max-width: 1199px) {
  .b-services-carousel {
    padding: 0 50px;
  }
  .b-services-carousel .owl-prev::before,
  .b-services-carousel .owl-next::before {
    font: 45px "FontAwesome";
    position: absolute;
    top: 24%;
    color: #f6cec1;
  }
  .b-services-carousel .owl-prev:hover::before,
  .b-services-carousel .owl-next:hover::before {
    color: #f0ab94;
  }
  .b-services-carousel .owl-next::before {
    content: "\f105";
    right: 0;
  }
  .b-services-carousel .owl-prev::before {
    content: "\f104";
    left: 0;
  }
}
.owl-carousel .owl-item .b-services-carousel__item img {
  -webkit-transform-style: flat;
}
@media (max-width: 575px) {
  .b-services-carousel {
    padding: 0;
    margin-bottom: 50px;
  }
}



