.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-service-link {
  color: #d8d3ab;
  display: block;
  font: 20px/25px "BlissPro";
  margin: 0 0 10px 50px;
}
.b-service-link:hover,
.b-service-link:focus,
.b-service-link:active {
  color: #fff;
}
@media (max-width: 992px) {
  .b-service-link {
    font: 18px/25px "BlissPro";
    margin: 0 0 10px;
  }
}



