.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-search-results {
  margin: 20px 0;
  font-weight: 100;
}
.b-search-results__top {
  margin-bottom: 20px;
}
.b-search-result {
  padding: 20px 0;
  border-top: 2px solid #e5e7dc;
}
.b-search-result__link {
  color: #1b5773;
  font-size: 18px;
  font-family: "BlissPro", sans-serif;
}



