.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-phone--header {
  font: 31px/80px "BlissProLight";
  color: #000;
  float: left;
  margin-right: 20px;
  margin-left: -40px;
}
@media (max-width: 1199px) {
  .b-phone--header {
    font: 28px/75px "BlissProLight";
    margin-left: 0;
  }
}
@media screen and (max-width: 1250px) {
  .b-phone--header {
    margin-left: -80px;
  }
}
@media screen and (max-width: 1200px) {
  .b-phone--header {
    margin-left: -40px;
  }
}
@media (max-width: 992px) {
  .b-phone--header {
    font: 22px/75px "BlissProLight";
    margin-top: 10px;
  }
}
@media (max-width: 767px) {
  .b-phone--header {
    text-align: center;
    float: none;
    font: 26px/40px "BlissProLight";
    margin-top: 15px;
    margin-right: 0;
  }
}



