.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-address-gallery {
  height: 300px;
  position: relative;
}
.b-address-gallery .owl-item {
  text-align: center;
}
.b-address-gallery .owl-stage-outer {
  height: 300px;
  overflow: hidden;
}
.b-address-gallery .owl-stage {
  height: 300px;
}
.b-address-gallery .owl-item {
  display: inline-block;
}
.b-address-gallery .owl-dots {
  display: none;
}
.b-address-gallery .owl-next,
.b-address-gallery .owl-prev {
  position: absolute;
  top: 50%;
  width: 40px;
  height: 80px;
  background: #fff;
  display: block;
  margin-top: -40px;
  cursor: pointer;
}
.b-address-gallery .owl-next.disabled,
.b-address-gallery .owl-prev.disabled {
  display: none;
}
.b-address-gallery .owl-next::before,
.b-address-gallery .owl-prev::before {
  font: 56px/76px "FontAwesome";
  color: #f6cec1;
  text-align: center;
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
}
.b-address-gallery .owl-next:hover::before,
.b-address-gallery .owl-prev:hover::before {
  color: #f0ab94;
}
.b-address-gallery .owl-prev {
  left: 0;
  border-radius: 0 40px 40px 0;
}
.b-address-gallery .owl-prev::before {
  left: 2px;
  content: "\f104";
}
.b-address-gallery .owl-prev:hover {
  box-shadow: 3px 0 2px rgba(0, 0, 0, 0.2);
}
.b-address-gallery .owl-next {
  right: 0;
  border-radius: 40px 0 0 40px;
}
.b-address-gallery .owl-next::before {
  left: 17px;
  content: "\f105";
}
.b-address-gallery .owl-next:hover {
  box-shadow: -3px 0 2px rgba(0, 0, 0, 0.2);
}
.b-gallery__item {
  width: 424px;
  display: inline-block;
  max-width: 100%;
}
@media (max-width: 460px) {
  .b-gallery__item {
    width: 100%;
  }
  .b-address-gallery .b-gallery__img {
    background-size: contain;
  }
}



