.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-feature__title {
  font: 56px "WaldorfLightItalic";
  color: #a03339;
  left: -230px;
  top: 90px;
  transform: translate3d(150px, 0, 0);
  opacity: 0;
  transition: transform 0.7s ease-out, opacity 0.4s;
  position: absolute;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
  max-width: 810px;
}
.b-feature__image {
  background-size: cover;
  height: 400px;
}
.active .b-feature .b-feature__title {
  opacity: 1;
  transform: translate3d(0, 0, 0);
}
.b-feature__text {
  margin: 190px 0 0 20px;
  padding: 0;
  overflow: hidden;
  font: 18px/21px "BlissProLight";
  color: #626365;
}
.b-feature__links {
  margin-left: 22px;
}
.b-feature__link {
  margin-right: 40px;
  white-space: nowrap;
  text-decoration: none;
  border-bottom: 1px solid #1b5773;
}
.b-feature__link:hover {
  border: none;
}
.b-feature__link.b-link--with-arrow::after {
  position: absolute;
}
@media (max-width: 992px) {
  .b-feature__title {
    font: 50px "WaldorfLightItalic";
    left: -170px;
    top: 90px;
    max-width: 590px;
  }
  .b-feature__text {
    margin-top: 180px;
  }
}
@media (max-width: 767px) {
  .b-feature__link {
    font-size: 18px;
    display: block;
  }
  .b-feature__link::after {
    display: none;
  }
  .b-feature__text {
    height: 170px;
  }
}
@media (max-width: 575px) {
  .b-feature__title {
    left: 0;
    right: 0;
    top: -220px;
    font-size: 30px;
    text-align: center;
  }
  .b-feature__image {
    margin-top: 60px;
    height: 170px;
  }
  .b-feature__links {
    margin-left: 22px;
  }
  .b-feature__text {
    margin: 15px 20px 90px;
    font: 16px/25px "BlissProLight";
    color: #7e7e7e;
    height: 150px;
  }
  .b-feature__link {
    display: inline-block;
  }
}



