.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-main-carousel-item {
  height: 400px;
}
.b-main-carousel-item::after {
  content: '';
  position: absolute;
  background: #000;
  opacity: 0.3;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: none;
  z-index: 1;
}
.b-main-carousel-item__description {
  position: absolute;
  top: 58px;
  width: 500px;
  left: 100px;
  text-align: center;
  color: #fff;
  z-index: 2;
}
.b-main-carousel-item__title {
  font: 60px/70px "WaldorfItalic";
  max-height: 220px;
  overflow: hidden;
}
.b-main-carousel-item__text {
  font: 20px/30px "BlissPro";
  padding: 10px 120px;
}
@media (max-width: 992px) {
  .b-main-carousel-item__description {
    top: 45px;
    width: 370px;
    left: 50px;
    max-height: 350px;
    overflow: hidden;
  }
  .b-main-carousel-item__text {
    padding: 10px 50px;
  }
}
@media (max-width: 900px) {
  .b-main-carousel-item {
    background-position: 80% 0;
  }
}
@media (max-width: 767px) {
  .b-main-carousel-item::after {
    display: block;
  }
  .b-main-carousel-item__description {
    text-shadow: 0 0 1px #000;
  }
}
@media (max-width: 575px) {
  .b-main-carousel-item__title {
    font-size: 46px;
  }
  .b-main-carousel-item__description {
    top: 25px;
    width: auto;
    left: 0;
    right: 0;
    min-height: 320px;
  }
  .b-main-carousel-item__text {
    padding: 10px 50px;
  }
}
.b-main-carousel {
  height: 400px;
}
.b-main-carousel .owl-dots {
  display: none;
}
.b-main-carousel .owl-next,
.b-main-carousel .owl-prev {
  position: absolute;
  top: 50%;
  width: 40px;
  height: 80px;
  background: #fff;
  display: block;
  margin-top: -40px;
}
.b-main-carousel .owl-next::before,
.b-main-carousel .owl-prev::before {
  font: 56px/76px "FontAwesome";
  color: #f6cec1;
  text-align: center;
  display: block;
  position: absolute;
  top: 0;
  bottom: 0;
}
.b-main-carousel .owl-next:hover::before,
.b-main-carousel .owl-prev:hover::before {
  color: #f0ab94;
}
.b-main-carousel .owl-prev {
  left: 0;
  border-radius: 0 40px 40px 0;
}
.b-main-carousel .owl-prev::before {
  left: 2px;
  content: "\f104";
}
.b-main-carousel .owl-prev:hover {
  box-shadow: 3px 0 2px rgba(0, 0, 0, 0.2);
}
.b-main-carousel .owl-next {
  right: 0;
  border-radius: 40px 0 0 40px;
}
.b-main-carousel .owl-next::before {
  left: 17px;
  content: "\f105";
}
.b-main-carousel .owl-next:hover {
  box-shadow: -3px 0 2px rgba(0, 0, 0, 0.2);
}
@media (max-width: 575px) {
  .b-main-carousel .owl-next,
  .b-main-carousel .owl-prev {
    display: none;
  }
  .b-main-carousel .owl-dots {
    display: block;
    bottom: 20px;
  }
}



