.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-footer {
  background: #1b5773;
  color: #638294;
  padding: 50px 200px 10px;
  margin: 0;
}
.b-footer__text {
  font: 13px/20px "BlissPro";
  padding-right: 30px;
}
.b-footer__text a {
  color: #919586;
}
.b-footer__text a:active,
.b-footer__text a:focus,
.b-footer__text a:hover {
  color: #fff;
}
@media (max-width: 1420px) {
  .b-footer {
    padding: 50px 100px 10px;
  }
}
@media (max-width: 1420px) {
  .b-footer {
    padding: 50px 50px 10px;
  }
}
@media (max-width: 1023px) {
  .b-footer__col-left {
    width: 100% !important;
  }
  .b-footer__col-right {
    width: 100% !important;
    margin-top: 30px;
  }
  .b-footer__col-right::after {
    content: "";
    display: block;
    clear: both;
  }
  .b-footer__text {
    width: 50%;
    float: left;
  }
}
@media (max-width: 1199px) {
  .b-footer {
    padding: 50px 40px 10px;
  }
  .b-footer__text {
    padding-right: 0;
  }
}
@media (max-width: 575px) {
  .b-footer__text {
    width: 100%;
  }
}



