.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-services {
  margin: 30px 0;
}
.b-service {
  margin-bottom: 30px;
}
.b-service .b-table {
  margin-top: 0;
}
.b-service__title {
  font: 30px/25px "BlissProLight";
  color: #1b5773 !important;
  margin-bottom: 23px;
}
.b-service__price-cell {
  white-space: nowrap;
}



