.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-news-list-item {
  height: 400px;
  width: 250px;
  padding-left: 30px;
}
.b-news-list-item__img {
  display: none !important;
}
.b-news-list-item__content {
  padding: 40px 0;
}
.b-news-list-item__title {
  font: 20px/25px "BlissPro";
}
.b-news-list-item__title a {
  color: #1b5773;
}
.b-news-list-item__text {
  color: #919586;
  font: 17px/21px "BlissProLight";
  max-height: 223px;
  overflow: hidden;
  margin-top: 20px;
}
.b-news-list-item__date {
  font: 14px "WaldorfItalic";
  color: #1b5773;
  margin-top: 60px;
}
.b-news-list-item__date::after {
  content: "";
  display: block;
  width: 1px;
  height: 91px;
  background: #cecfb9;
  top: 10px;
  left: -30px;
  position: absolute;
}
.b-news-list-item__date span {
  font: 40px "WaldorfItalic";
  float: left;
  margin: -4px 15px 0 0;
}
.b-news-list .owl-item:nth-child(2) .b-news-list-item__date::after {
  display: none;
}
@media (min-width: 768px) {
  .b-news-list-item--selected {
    width: 600px;
    box-shadow: 0 30px 0 #fff inset;
    background: #a03339;
    color: #f6cec1;
    padding-left: 0;
  }
  .b-news-list-item--selected:hover .b-news-list-item__img {
    box-shadow: 0 5px 8px rgba(0, 0, 0, 0.1);
    transform: translate3d(0, -5px, 0);
  }
  .b-news-list-item--selected .b-news-list-item__img {
    box-shadow: 0 0 0 rgba(0, 0, 0, 0);
    transform: translate3d(0, 0, 0);
    transition: transform 0.2s, box-shadow 0.2s;
    width: 83%;
    height: 160px;
    display: block !important;
    margin: 0 auto;
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
  }
  .b-news-list-item--selected .b-news-list-item__content {
    padding: 0 30px 30px 120px;
  }
  .b-news-list-item--selected .b-news-list-item__title {
    margin: 40px 0 30px;
    font: 20px "BlissProLight";
  }
  .b-news-list-item--selected .b-news-list-item__title a {
    color: #fff;
  }
  .b-news-list-item--selected .b-news-list-item__text {
    max-height: 130px;
    overflow: hidden;
    color: #f6cec1;
    font: 16px/21px "BlissProLight";
  }
  .b-news-list-item--selected .b-news-list-item__date {
    left: 20px;
    top: 200px;
    color: #d8d3ab;
    width: 80px;
    margin-top: 0;
    position: absolute;
  }
  .b-news-list-item--selected .b-news-list-item__date::after {
    content: "";
    display: block;
    width: 1px;
    height: 100px;
    background: #b46768;
    right: 0;
    top: 0;
    left: auto;
    position: absolute;
  }
  .b-news-list-item--selected .b-news-list-item__date span {
    display: block;
    float: none;
    margin: 0;
  }
}
@media (max-width: 1199px) {
  .b-news-list-item--selected {
    width: 480px;
  }
  .b-news-list-item--selected .b-news-list-item__text {
    max-height: 90px;
  }
  .b-news-list-item__img {
    width: 401px;
  }
}
@media (max-width: 1070px) {
  .b-news-list-item {
    width: 400px;
  }
  .b-news-list-item__date::after {
    display: none;
  }
  .b-news-list-item--selected {
    width: 480px;
  }
}
@media (max-width: 992px) {
  .b-news-list-item {
    width: auto !important;
  }
}
@media (max-width: 575px) {
  .b-news-list-item {
    height: 175px;
    padding-bottom: 30px;
  }
  .b-news-list-item__content {
    clear: both;
    padding: 20px 0 0;
  }
}
@media (max-width: 767px) {
  .b-news-list-item__text {
    max-height: none !important;
  }
  .b-news-list-item__date {
    margin-top: 10px;
  }
}
@media (max-width: 470px) {
  .b-news-list-item__text {
    display: none;
  }
}
.b-news {
  margin: 20px 0;
}
.b-news__title {
  margin: 0 30px 0 50px;
  display: inline-block;
}
.b-news-list {
  margin: 30px 0;
}
.b-news-list .owl-item {
  padding-top: 10px;
}
@media (max-width: 575px) {
  .b-news {
    margin-bottom: 110px;
    position: relative;
  }
  .b-news::after {
    content: "";
    position: absolute;
    bottom: -50px;
    left: 15px;
    right: 15px;
    height: 2px;
    background: #f6cec1;
    display: block;
  }
  .b-news__title {
    margin: 0 30px 0 50px;
    display: block;
    text-align: center;
  }
  .b-news__link {
    bottom: -100px;
    left: 0;
    right: 0;
    text-align: center;
    position: absolute;
    display: block;
  }
  .b-news-list {
    margin: 0;
  }
}



