.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-doctor-card {
  margin: 20px 0;
  text-align: left;
  color: #404040;
}
.b-doctor-card__img {
  width: 150px;
  height: 200px;
  float: left;
  margin: 0 25px 20px 0;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
.b-doctor-card__link {
  color: #1b5773;
  display: block;
}
.b-doctor-card__btn {
  margin-top: 20px;
}
.b-doctor-card::after {
  content: "";
  clear: both;
  height: 1px;
  display: block;
}



