.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-review {
  margin: 18px 0;
  padding: 30px 0 20px;
  border-top: 2px solid #e6e7dc;
}
.b-review__title {
  font: 21px "WaldorfLightItalic";
  color: #000;
  margin-bottom: 10px;
}
.b-review__date {
  font-size: 16px;
  margin-right: 5px;
  display: inline-block;
}
.b-review__text {
  font: 17px/25px "BlissProLight";
  color: #626365;
}
.b-review__response {
  margin-top: 30px;
  font: 17px/25px "BlissProLight";
  color: #919586;
}



