.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-categories {
  margin: 30px 0;
}
.b-category {
  margin-bottom: 30px;
}
.b-category__img {
  height: 240px;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
  margin-bottom: 10px;
  display: block;
}
.b-category__link {
  font: 18px/25px "BlissPro";
  color: #1b5773;
}



