.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-heading {
  font: 40px/45px "BlissProLight";
  color: #000 !important;
  text-transform: uppercase;
}
.b-heading--secondary {
  font: 30px/35px "BlissProLight";
  color: #7e7e7e !important;
  text-transform: none;
  margin-bottom: 20px;
  margin-top: 20px;
}
.b-heading--black {
  color: #000 !important;
}
.b-heading__link {
  display: inline-block;
  color: #a03339 !important;
  text-decoration: underline;
  font: 21px/26px "WaldorfLightItalic";
  text-transform: none;
  margin-left: 15px;
}
@media (max-width: 767px) {
  .b-heading__link {
    display: block;
    margin-left: 0;
    margin-top: 10px;
  }
}
@media (max-width: 575px) {
  .b-heading {
    font-size: 26px;
    line-height: 34px;
  }
}



