.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-contacts {
  margin: 30px 0;
}
.b-contact {
  border-bottom: 2px solid #e6e7dc;
}
.b-contact--list-page {
  margin-bottom: 30px;
  padding-bottom: 35px;
}
.b-contact img {
  width: 100%;
}
.b-contact__title {
  font: 30px/25px "BlissProLight";
  color: #1b5773 !important;
  margin-bottom: 23px;
}
.b-contact--card {
  border-bottom: none;
}
.b-contact__text {
  margin: 30px 0;
}
.half-bordered {
  position: relative;
}
.half-bordered::after {
  content: "";
  display: block;
  position: absolute;
  height: 2px;
  border-bottom: 2px solid #e6e7dc;
  bottom: 0;
  right: 0;
  left: 51%;
}
@media (max-width: 575px) {
  .half-bordered::after {
    display: none;
  }
}



