.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-cards {
  margin: 30px 0;
}
.b-cards__search-panel {
  margin-left: 0;
  margin-right: 0;
}
.b-card {
  text-align: center;
}
.b-card__img {
  background-position: center;
  background-size: contain;
  background-repeat: no-repeat;
  height: 200px;
  width: 150px;
  margin: 0 auto 20px;
}
.b-card__title {
  color: #1b5773;
  font: 20px "BlissPro-Bold";
  margin-bottom: 10px;
  padding-left: 20px;
  padding-right: 20px;
}
.b-card__sub {
  color: #919586;
  font: 15px "BlissProLight";
  padding-left: 20px;
  padding-right: 20px;
}
.b-card--doctor {
  height: 360px;
}
.b-card--equipment {
  height: 300px;
}
.b-card--category {
  height: 230px;
}
.b-card__min-sum {
  color: #a03339;
  text-align: left;
  position: relative;
  margin: -12px 0 0;
  font-size: 14px;
}
.b-card--doctor {
  position: relative;
}
.b-card--doctor:hover {
  background: #fbf0ec;
}
.b-card--doctor:hover::before {
  display: block;
}
.b-card--doctor:hover .b-card__btn-panel {
  display: block;
}
.b-card--doctor::before {
  content: '';
  position: absolute;
  display: none;
  width: 100%;
  background: #fbf0ec;
  height: 20px;
  top: -20px;
  left: 0;
}
.b-card__btn-panel {
  display: none;
  background: #fbf0ec;
  position: absolute;
  width: 100%;
  height: 100px;
  z-index: 100;
}
.b-cards--equipment .b-card__img {
  height: 200px;
  width: auto;
  border: 1px solid #ddd;
  background-size: cover;
  cursor: pointer;
}
.b-cards--equipment .b-card__title {
  max-height: 52px;
  overflow: hidden;
  cursor: pointer;
}
.b-cards--categories .b-card__img {
  height: 156px;
  width: auto;
  background-size: cover;
  margin-bottom: 10px;
  display: block;
}
.b-cards--categories .b-card__title {
  font: 18px "BlissPro";
  text-align: left !important;
  padding: 0;
  display: block !important;
  max-height: 46px;
  overflow: hidden;
}
.b-card--module-img {
  text-align: center;
  max-width: 100%;
}



