.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-reviews {
  margin: 10px 0 0;
}
.b-reviews--btn-link {
  margin-left: 30px;
  display: inline-block;
  position: relative;
  top: -7px;
  font-family: "BlissPro-Bold";
}
.b-reviews--clinics .b-review img {
  border: 1px solid #ddd;
}
.b-reviews .b-pager {
  margin-bottom: 10px;
}
@media (max-width: 992px) {
  .b-reviews--btn-link {
    display: block;
    margin-left: 0;
  }
}



