.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-aside-form {
  padding: 20px 0 20px 25px;
  font-size: 15px;
}
.b-aside-form .b-form__group {
  margin-bottom: 10px;
}



