.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-sales {
  margin: 30px 0;
}
.b-sales--archive .b-sale {
  border-bottom: none !important;
}
.b-sales--archive .b-sale__date {
  color: #a03339 !important;
}
.b-sale {
  margin-bottom: 35px;
  padding-bottom: 35px;
  border-bottom: 2px solid #e6e7dc;
}
.b-sale--card {
  border-bottom: none;
  margin-bottom: 0;
  padding-bottom: 0;
}
.b-sale__left {
  padding-right: 0;
}
.b-sale__right {
  padding-left: 35px;
  margin-top: 20px;
}
.b-sale__right--articles {
  margin-top: -12px;
}
.b-sale img {
  width: 100%;
}
.b-sale__date {
  font: 16px "WaldorfLightItalic";
  color: #1b83a0;
  margin-bottom: 10px;
}
.b-sale__link {
  font: 18px/40px "BlissPro-Bold";
  color: #1b5773 !important;
  margin-bottom: 10px;
  display: block;
}
.b-sale__warning {
  font: 20px/20px "BlissPro-Bold";
  margin-bottom: 10px;
  color: #a03339;
}
.b-sale__text {
  font: 17px/25px "BlissProLight";
  color: #626365;
}
.b-sale__content {
  margin: 30px 0 20px;
}
.b-sale__pager {
  border-bottom: 2px solid #e6e7dc;
  border-top: 2px solid #e6e7dc;
  margin: 30px 0;
  padding: 35px 0;
}
.b-sale__pager::after {
  content: " ";
  display: block;
  clear: both;
  width: 100%;
  height: 1px;
}
.b-sale__link-prev {
  padding-left: 43px;
  position: relative;
  line-height: 20px;
  display: inline-block;
  color: #1b83a0 !important;
}
.b-sale__link-prev::before {
  content: "";
  width: 16px;
  height: 16px;
  background: url(~img/common/arr-left-red.png);
  position: absolute;
  top: 13px;
  left: 0;
  display: block;
}
.b-sale__link-next {
  float: right;
  padding-right: 43px;
  position: relative;
  line-height: 20px;
  text-align: right;
  display: inline-block;
  color: #1b83a0 !important;
}
.b-sale__link-next::before {
  content: "";
  width: 16px;
  height: 16px;
  background: url(~img/common/arr-right-red.png);
  position: absolute;
  top: 13px;
  right: 0;
  display: block;
}
@media (max-width: 575px) {
  .b-sale__left {
    padding-right: 15px;
  }
  .b-sale__right.b-sale__right--detail-page {
    padding-left: 15px;
  }
}



