.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.breadcrumb {
  background: none;
  margin-top: 30px;
}
.breadcrumb-item {
  color: #919586;
  font: 16px "BlissPro";
  position: relative;
  margin-right: 18px;
  text-decoration: none;
  border-bottom: 1px solid #1b5773;
}
.breadcrumb-item:hover {
  border-bottom: none;
  text-decoration: none;
}
.breadcrumb-item.active {
  border-bottom: none;
}
.breadcrumb-item::before {
  position: absolute;
  left: -16px;
  color: #f6cec1 !important;
  padding-left: 5px !important;
  padding-right: 5px !important;
}
@media (max-width: 470px) {
  .breadcrumb {
    padding: 0;
  }
}



