.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-content-slider {
  margin: 20px 0;
  padding-top: 100px;
}
.b-content-slider__item-title {
  text-align: center;
  color: #a03339;
  font: 30px/37px "WaldorfLightItalic";
  margin-bottom: 20px;
}
.b-content-slider__item p {
  margin-bottom: 20px;
}
.b-content-slider .owl-dots {
  counter-reset: dots;
  position: absolute;
  top: 0 !important;
  bottom: auto;
  left: 0 !important;
  right: 0 !important;
  text-align: center;
}
.b-content-slider .owl-dot {
  background: none !important;
  transform: none !important;
  display: inline-block;
  font: 40px "WaldorfItalic";
  width: 50px !important;
  position: relative;
  color: #1b83a0;
}
.b-content-slider .owl-dot.active {
  color: #a03339;
}
.b-content-slider .owl-dot.active::after {
  position: absolute;
  top: -4px;
  left: -5px;
  content: "";
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border: 1px solid #f6cec1;
}
.b-content-slider .owl-dot:hover {
  color: #a03339;
}
.b-content-slider .owl-dot::before {
  counter-increment: dots;
  content: counter(dots);
}



