.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-main-features {
  margin: 60px 0 20px 50px;
  padding-bottom: 70px;
  position: relative;
}
.b-main-features::after {
  bottom: 0;
  left: 0;
  right: 0;
  height: 2px;
  background: #f6cec1;
  content: "";
  display: block;
  position: absolute;
}
.b-main-features__title {
  margin-bottom: 35px;
}
.b-main-features .owl-dots {
  counter-reset: dots;
  position: absolute;
  top: 0 !important;
  bottom: auto;
  left: 50% !important;
  margin-left: 35px !important;
  text-align: left;
}
.b-main-features .owl-dot {
  background: none !important;
  transform: none !important;
  display: inline-block;
  font: 40px "WaldorfItalic";
  width: 50px !important;
  position: relative;
}
.b-main-features .owl-dot.active {
  color: #a03339;
}
.b-main-features .owl-dot.active::after {
  position: absolute;
  top: -4px;
  left: -19px;
  content: "";
  width: 60px;
  height: 60px;
  border-radius: 50%;
  border: 1px solid #f6cec1;
}
.b-main-features .owl-dot:hover {
  color: #a03339;
}
.b-main-features .owl-dot::before {
  counter-increment: dots;
  content: counter(dots);
}
@media (max-width: 992px) {
  .b-main-features__title {
    margin-left: 38px;
  }
}
@media (max-width: 992px) {
  .b-main-features {
    margin: 60px 0 20px;
  }
  .b-main-features .owl-dots {
    counter-reset: dots;
    position: absolute;
    top: 0 !important;
    bottom: auto;
    left: 50% !important;
    margin-left: 35px !important;
    text-align: left;
  }
  .b-main-features .owl-dot {
    font: 30px "WaldorfItalic";
    width: 20px;
  }
  .b-main-features .owl-dot.active::after {
    top: -2px;
    left: -13px;
    width: 41px;
    height: 41px;
  }
}
@media (max-width: 575px) {
  .b-main-features {
    padding-bottom: 30px;
  }
  .b-main-features::after {
    left: 10px;
    right: 10px;
  }
  .b-main-features__title {
    margin-bottom: 35px;
    text-align: center;
    font: 26px "BlissProLight";
  }
  .b-main-features .owl-dots {
    top: auto !important;
    bottom: 60px !important;
    left: 0 !important;
    margin-left: 35px !important;
    text-align: left;
  }
  .b-main-features .owl-dot {
    font-size: 20px;
    width: 28px !important;
  }
  .b-main-features .owl-dot.active::after {
    top: -8px;
    left: -16px;
    width: 41px;
    height: 41px;
  }
}



