.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.l-header {
  height: 80px;
}
.b-header {
  padding: 0;
  height: 80px;
  position: fixed;
  top: 0;
  background: #fff;
  z-index: 9999;
  box-shadow: 1000px 0 0 #fff, -1000px 0 0 #fff;
}
.b-header .b-text-line {
  margin-top: 24px;
  margin-bottom: 3px;
  color: #6c6c6c;
  font: 12px/12px "BlissPro";
}
.b-header-buttons {
  height: 80px;
  vertical-align: middle;
  display: table-cell;
}
.b-header-buttons .b-btn--ask-question {
  margin-right: 21px;
}
.b-header-buttons {
  position: relative;
}
.b-header-buttons-btn-feedback {
  display: none;
}
@media (max-width: 992px) {
  .b-header-buttons-btn-feedback {
    display: inline-block;
  }
  .b-header-buttons__panel {
    background: rgba(27, 87, 115, 0.9);
    position: absolute;
    display: none;
    top: 64px;
    height: 152px;
    text-align: center;
    width: 222px;
    left: 50%;
    transform: translateX(-50%);
    padding: 10px;
    border-radius: 5px;
  }
  .b-header-buttons__panel .btn {
    margin: 0 0 10px;
  }
  .b-header-buttons__panel.active {
    display: block;
  }
}
@media (max-width: 1199px) {
  .b-header .b-text-line {
    top: 10px;
    left: 30px;
    margin: 0;
    position: absolute;
  }
}
@media (max-width: 992px) {
  .b-header .b-text-line {
    top: 18px;
    left: 15px;
  }
  .b-header-col--2 {
    padding-left: 0;
    padding-right: 0;
  }
  .b-header-col--3 {
    padding-left: 0;
    padding-right: 0;
  }
  .b-header .btn {
    font-size: 15px;
  }
  .b-header .btn .fa {
    font-size: 13px;
  }
}
@media (max-width: 767px) {
  .l-header {
    height: 245px;
  }
  .b-header {
    height: 145px;
    border-bottom: 2px solid #f6cec1;
    position: static !important;
  }
  .b-header-col {
    position: static;
  }
  .b-header-col--1 {
    top: 160px;
    left: 0;
    right: 0;
    padding: 0;
    position: absolute;
  }
  .b-header-col--1 img {
    margin: 0 auto;
  }
  .b-header-col--2 {
    position: static;
  }
  .b-header-col--3 {
    text-align: center;
  }
  .b-header .b-text-line {
    position: static;
  }
  .b-header-buttons {
    text-align: center;
    margin-top: 15px;
    height: auto;
    display: block;
  }
}



