.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.blockquote {
  color: #a6a6a6;
  font: 21px/30px "WaldorfItalic";
  border: none;
  margin: 20px 0;
  text-align: center;
}
.blockquote--dark {
  color: #7e7e7e;
}
.blockquote strong,
.blockquote b {
  color: #000;
}
.blockquote--left {
  text-align: left;
  padding-left: 0;
}



