.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-footer-menu {
  display: block;
}
.b-footer-menu .owl-dot {
  background: #fff;
}
.b-footer-menu .owl-dot.active {
  background: #d8d3ab;
}
.b-footer-menu-group__title {
  color: #fff;
  border-bottom: 2px solid #919586;
  padding: 0 0 20px;
  margin-bottom: 20px;
  width: 260px;
  font: 30px "BlissProLight";
}
.b-footer-menu-group__link {
  display: block;
  color: #d8d3ab;
  margin-bottom: 3px;
  font: 16px "BlissProLight";
}
.b-footer-menu-group__link:active,
.b-footer-menu-group__link:focus,
.b-footer-menu-group__link:hover {
  color: #fff;
}
@media (max-width: 1199px) {
  .b-footer-menu-group__title {
    width: 250px;
    font-size: 28px;
  }
}
@media (max-width: 992px) {
  .b-footer-menu-group {
    margin-bottom: 20px;
  }
  .b-footer-menu-group__title {
    font-size: 27px;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 100%;
    display: inline-block;
  }
}
@media (max-width: 767px) {
  .b-footer-menu-group__title {
    font-size: 21px;
  }
}
@media (max-width: 575px) {
  .b-footer-menu-group__title {
    font-size: 27px;
  }
}



