.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-tabs {
  margin: 20px 0;
}
.b-tabs .nav-tabs {
  border-bottom: 2px solid #e6e7dc;
}
.b-tabs .nav-link {
  border-radius: 0 !important;
  font: 18px/20px "BlissPro";
  color: #388da7;
}
.b-tabs .nav-link--selected {
  color: #fff;
  background: #a03339;
}
.b-tabs .nav-tabs .nav-link.active,
.b-tabs .nav-tabs .nav-link.active:focus,
.b-tabs .nav-tabs .nav-link.active:hover,
.b-tabs .nav-tabs .nav-item.open .nav-link,
.b-tabs .nav-tabs .nav-item.open .nav-link:focus,
.b-tabs .nav-tabs .nav-item.open .nav-link:hover {
  background-color: #e6e7dc;
  border-color: #e6e7dc #e6e7dc transparent;
  color: #1b5773;
}
.tab-content {
  padding: 20px 0;
}
@media (max-width: 1199px) {
  .b-tabs .nav-link {
    font-size: 16px;
  }
}
@media (max-width: 992px) {
  .b-tabs .nav-link {
    font-size: 14px;
    padding-left: 6px;
    padding-right: 6px;
  }
}
@media (max-width: 575px) {
  .b-tabs .nav-tabs {
    border-bottom: none;
  }
  .b-tabs .nav-item {
    display: block;
    float: none;
  }
  .b-tabs .nav-link {
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    max-width: 100%;
    display: inline-block;
    font-size: 18px;
    text-align: center;
    display: block;
  }
}



