.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-tree-list {
  list-style: none;
  margin: 10px 0 20px 20px;
  padding: 0;
  display: block;
}
.b-tree-list ul {
  list-style: none;
  display: none;
  margin: 10px 0;
  padding: 0;
  color: #7e7e7e;
  font: 17px "BlissProLight";
}
.b-tree-list > li {
  color: #1b5773;
  font: 18px "BlissPro";
  cursor: pointer;
  position: relative;
}
.b-tree-list > li::before {
  font-weight: 100;
  content: "+";
  color: rgba(27, 87, 115, 0.5);
  width: 20px;
  top: 0;
  left: -20px;
  display: inline-block;
  position: absolute;
}
.b-tree-list > li:hover::before {
  color: #1b5773;
}
.b-tree-list > li.open {
  color: #a03339;
}
.b-tree-list > li.open::before {
  content: "";
  color: rgba(27, 87, 115, 0.5);
}
.b-tree-list > li.open:hover::before {
  color: #1b5773;
}
.b-tree-list > li.open ul {
  display: block;
}
.b-tree-list__item--empty::before {
  display: none !important;
}



