.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-diseases {
  margin: 30px 0;
}
.b-diseases__top {
  border-bottom: 2px solid #e6e7dc;
  margin-bottom: 40px;
  padding-bottom: 8px;
}
.b-diseases__top a {
  font: 18px "BlissProLight";
  color: #1b5773;
  text-decoration: none !important;
  display: inline-block;
}
.b-diseases__top a.empty {
  color: #e6e7dc;
}
.b-diseases__top a + a {
  margin-left: 20px;
}
.b-diseases__list a {
  color: #1b5773;
  font: 21px/40px "WaldorfLightItalic";
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
  display: block;
}
.b-diseases__group {
  border-bottom: 1px solid #e6e7dc;
  padding-bottom: 20px;
  margin-bottom: 10px;
}
.b-diseases__letter {
  font: 30px/25px "BlissProLight";
  color: #7e7e7e;
  margin-bottom: 20px;
}
@media (max-width: 1199px) {
  .b-diseases__top a + a {
    margin-left: 10px;
  }
}



