.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-with-metro-icon {
  position: relative;
}
.b-with-metro-icon-img {
  position: absolute;
  width: auto !important;
  left: -18px;
  top: 6px;
}
@media (max-width: 575px) {
  .b-with-metro-icon {
    position: relative;
    margin-left: 16px;
  }
}



