.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-gallery {
  margin: 10px 0;
}
.b-gallery__item {
  display: block;
  margin-bottom: 25px;
}
.b-gallery__item--video {
  position: relative;
}
.b-gallery__item--video::before {
  content: "";
  display: block;
  position: absolute;
  left: 50%;
  top: 150px;
  transform: translate3d(-50%, -50%, 0) scale(1);
  width: 80px;
  height: 80px;
  background-image: url(~img/common/play.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  transition: opacity 0.3s, transform 0.15s;
  opacity: 1;
  pointer-events: none;
}
.b-gallery__item--video:hover::before {
  opacity: 0.5;
}
.b-gallery__item--video:active::before {
  transform: translate3d(-50%, -50%, 0) scale(0.9);
}
.b-gallery__link {
  font: 18px "BlissPro";
  color: #1b5773 !important;
  display: block;
  margin: 5px 0 15px;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
  white-space: normal;
}
.b-gallery__img {
  margin: 0;
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
  display: block;
  height: 300px;
}
.b-gallery__img--hidden {
  display: none;
}
@media (max-width: 575px) {
  .b-gallery__item--video .modal--lg .modal-content {
    padding: 0 10px;
  }
}
.mfp-image-holder .mfp-content {
  z-index: 2010;
}



