.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
.b-link {
  font-family: "WaldorfLightItalic", sans-serif;
  color: #1b5773;
  font-size: 21px;
  line-height: 25px;
  font-style: italic;
  text-decoration: underline;
}
.b-link:active,
.b-link:focus,
.b-link:hover {
  color: #1b5773;
  text-decoration: none;
}
.b-link--block {
  font-family: "BlissPro-Bold", sans-serif;
  display: block;
  font-size: 18px;
  margin-bottom: 5px;
}
.b-link--with-arrow::after {
  font-family: "FontAwesome", sans-serif;
  content: "\f105";
  color: #d28e93;
  display: inline-block;
  margin-left: 10px;
  font-style: normal;
  text-decoration: none;
}
.b-link--1 {
  color: #d28e93;
}
.b-link--1:active,
.b-link--1:focus,
.b-link--1:hover {
  color: #d28e93;
}
.b-link--2 {
  color: #d8d3ab;
}
.b-link--2:active,
.b-link--2:focus,
.b-link--2:hover {
  color: #d8d3ab;
}
.b-link--red {
  font-size: inherit !important;
  color: #a03339 !important;
  font-family: inherit !important;
  font-style: inherit !important;
}



