@import "~bootstrap/dist/css/bootstrap.css";
@import "~font-awesome/css/font-awesome.css";
@import "~owl.carousel/dist/assets/owl.carousel.css";
@import "~magnific-popup/dist/magnific-popup.css";
@import "~pickmeup/css/pickmeup.css";
.pa {
  position: absolute;
}
.pr {
  position: relative;
}
.db {
  display: block;
}
.dib {
  display: inline-block;
}
.clear {
  clear: both;
}
.ellipsis {
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  max-width: 100%;
  display: inline-block;
}
body {
  font: 15px "BlissPro";
}
.container {
  width: 1230px;
}
.b-header {
  width: 1200px;
}
.l-container--page {
  margin-top: 20px;
  margin-bottom: 20px;
  font: 17px/25px "BlissProLight";
  color: #404040;
}
.l-container--page h1,
.l-container--page h3,
.l-container--page h2 {
  color: #7e7e7e;
}
.l-container--page a {
  color: #1b5773;
}
img {
  max-width: 100%;
}
/* fix strange behavior on Roman's firefox */
.dropdown-backdrop {
  display: none !important;
}
h1,
h2,
h3,
h4,
h5,
h6 {
  font-weight: normal;
}
.row-eq-height {
  display: table;
  table-layout: fixed;
  padding: 0;
  margin: 0;
  width: 100%;
}
.row-eq-height__col {
  padding: 0;
  display: table-cell;
  height: 100%;
  float: none;
}
.row-eq-height__col--services {
  background-color: #1b5773;
  background-image: url(~img/common/main-services/pattern.png);
}
.row-eq-height__col--contacts {
  background: #d8d3ab;
}
@media (max-width: 1199px) {
  .container {
    width: 1050px;
  }
  .b-header {
    width: 1020px;
  }
}
@media (max-width: 992px) {
  .container {
    width: 100%;
    padding: 0 !important;
    margin: 0 !important;
    overflow-x: hidden;
  }
  .container.l-container--page {
    padding: 0 15px !important;
  }
}
@media (max-width: 1051px) {
  .b-header {
    width: auto;
    padding-left: 20px !important;
    padding-right: 20px !important;
    left: 0;
    right: 0;
  }
}
@media (max-width: 767px) {
  .row-eq-height {
    display: block;
  }
  .row-eq-height__col {
    display: block;
    height: auto;
  }
}
@media (min-width: 767px) {
  .l-container--page > .row > .col-md-3 {
    padding-right: 0;
  }
  .l-container--page > .row > .col-md-9 {
    padding-left: 30px;
  }
}



